import React from "react";
import WrappedLoginForm from "../components/LoginForm.jsx";

class Login extends React.Component {
  render() {
    return (
      <div style={{ marginLeft: "40%", marginTop: "150px" }}>
        <WrappedLoginForm />
      </div>
    );
  }
}

export default Login;



